<template>
    <MenuComponent :key_menu="'1'">
        <a-modal v-model:open="open" title="" @ok="handleOk"
            cancelText="" okText=""
        >
            <br>
            <p></p>
            <p></p>
            <p></p>
        </a-modal>
        <div class="home-box">
            <a-typography>
                <a-typography-title></a-typography-title>
                <a-typography-paragraph>
                
                </a-typography-paragraph>
                <a-typography-paragraph>
                
                </a-typography-paragraph>
            </a-typography>
        </div>
    </MenuComponent>
</template>

<script>
import $ from 'jquery';
import {useStore} from 'vuex';
import router from '../router/index';

import MenuComponent from '@/components/MenuComponent.vue';
import {ref} from 'vue';

export default {
    name: 'HomeView',
    components: {
        MenuComponent,
    },
    setup() {
        const store = useStore();

        
        
        // 
        const open = ref(false);

        const handleOk = () => {
            router.push({name: 'editorinfo'});
        };

        $.ajax({
            url: "http://47.105.178.110:8000/user/get_status",
            type: "get",
            headers: {
                'Authorization': 'Bearer ' + store.state.user.access,
            },
            success(resp) {
                if (resp.result === 'success') {
                    // 
                    if (resp.data === '')
                    {
                        open.value = true;
                    }
                }
                else if (resp.msg ===  "Signature verification failed") {
                    store.state.user.is_login = false;
                    router.push({name:'login'});
                }
            },
            error(resp) {
                // console.log(resp);
                if (resp.msg !== '')
                    router.push({name:'login'});
                else
                    alert('');
            }
        });
        return {
            open,
            handleOk,
        }
    }
}
</script>

<style scoped>

.home-box {
    margin: 0 auto;
    width: 70%;
    padding: 20px;
    margin-top: 10%;
}

</style>